/** @jsx jsx */
import React, { useState } from "react"
import { Link, navigate } from "gatsby"
import { jsx, Input } from "theme-ui"
import {
  Search as IconSearch,
  XCircleFill as IconXCircleFill,
} from "react-bootstrap-icons"
import { useFlexSearch } from "react-use-flexsearch"
import { useSearchIndex } from "StaticQueries/search-index"

export default function SearchBar(props) {
  let urlParams

  typeof window !== "undefined" && window.location.search
    ? (urlParams = new URLSearchParams(window.location.search))
    : (urlParams = "")

  const searchHintsUrlPrefix = `?q=`
  const { index, store } = useSearchIndex()
  const [query, setQuery] = useState(urlParams === "" ? "" : urlParams.get("q"))
  const results = useFlexSearch(query, index, store)
  return (
    <>
      <div sx={{ display: "flex", position: "relative", alignItems: "center" }}>
        <Input
          id="searchInput"
          type="search"
          name="query"
          placeholder={props.placeholder}
          value={query}
          onChange={event => {
            if (window.location.pathname.indexOf("/search") !== -1) {
              navigate(`${searchHintsUrlPrefix}${event.target.value}`, {
                state: { query },
              })
            }

            setQuery(event.target.value)
          }}
          sx={{
            variant: [
              `element.size.${props.elementSize}`,
              "element.type.input",
              "input.filled",
            ],
            pl: "36px",
          }}
        />
        <div
          sx={{
            variant: "iconWrapper.medium",
            left: "8px",
          }}
        >
          <IconSearch size={16} sx={{ color: "gray_01" }} />
        </div>
        {query !== "" && (
          <div
            sx={{
              variant: "iconWrapper.medium",
              right: "8px",
            }}
          >
            <div
              onClick={event => {
                setQuery("")
              }}
              sx={{
                variant: "clickArea.medium",
              }}
            ></div>
            <IconXCircleFill size={16} sx={{ color: "gray" }} />
          </div>
        )}
      </div>

      {query !== "" && (
        <ul sx={{ listStyle: "none", paddingLeft: "0", pt: "16px" }}>
          {results.map(result => (
            <li key={result.id}>
              <Link
                to={`/${result.slug}`}
                sx={{
                  pt: "8px",
                  pb: "4px",
                  display: "grid",
                }}
              >
                {result.title}
              </Link>
              <p
                sx={{
                  color: "gray",
                  fontSize: "15px",
                  lineHeight: "20px",
                  ":hover": {
                    color: `gray`,
                  },
                }}
              >
                {`${result.body.slice(1, 156)} ...`}
              </p>
            </li>
          ))}
        </ul>
      )}
    </>
  )
}
